import { PropsWithChildren, useState } from "react"
import { Box, Typography } from "@rp/react/web/mui/core"
import { ArrowDropDown } from "@rp/react/web/mui/icons"

type Props = PropsWithChildren<{
  variant: "desktop" | "mobile"
}>
const MoreActions = ({ variant, children }: Props) => {
  const isMobile = variant === "mobile"
  const [isOpen, setIsOpen] = useState(true)

  if (!children) return null

  return (
    <Box sx={{ ml: isMobile ? 0 : 7, px: isMobile ? 2 : 0 }}>
      <Box
        sx={{ display: "flex", alignItems: "center", cursor: "pointer" }}
        onClick={() => setIsOpen(prev => !prev)}
      >
        <Typography sx={{ fontWeight: 700, py: 2.5 }}>More actions</Typography>
        <ArrowDropDown
          sx={{ transform: isOpen ? undefined : "rotateZ(-90deg)", transition: "transform 0.2s" }}
        />
      </Box>
      {isOpen ? children : null}
    </Box>
  )
}

export { MoreActions }
